import notify from './notifyFunction.js';

function setToLocalStorage(id, localStorageKeyName, listName) {
  let newValue = [];
  let checkedKeyValue = localStorage.getItem(`${localStorageKeyName}`); // assigns a key's value
  if (checkedKeyValue) {
    newValue = checkedKeyValue.split(',');
  }
  if (!newValue.includes(id)) {
    newValue.push(id);
    notify(`${listName}`);
  }
  localStorage.setItem(`${localStorageKeyName}`, newValue);
}
export default setToLocalStorage;
